@import url(https://fonts.googleapis.com/css2?family=Karla&family=Rubik&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

section {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    width: 500px;
    margin: auto;
}

.drag-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
}

.drag-container > div {
    margin-top: 25px;
    position: absolute;
    width: 200px;
    height: auto;
    top: 0;
}

.drag-east {
    left: 200px;
  }

.drag-west {
    right: 200px;
}

.header {
    font-size: 1.7rem;
    font-weight: 700;
    font-feature-settings: "c2sc";
    font-variant: small-caps;
}

.align-left {
    text-align: left;
}

.align-right {
    text-align: right;
}

.small-header {
    font-size: 1.2rem;
}

.drop-container {
    border: 2px solid black;
    border-radius: 2px;
    height: 408px;
    margin-top: 30px;
}

.drop-container .droppable {
    position: relative;
    width: 200px;
    height: 95px;
    right: 0;
    top: 10;
    border-bottom: 2px dotted black;
    padding-bottom: 5px;
}

.drop-container .droppable:nth-child(4) {
    border-bottom: none;
}

.LogoWrapper {
    display: flex;
    width: 100px;
}

.LogoWrapper div {
    display: flex;
}
