.EligibleDrop {
    background-color: var(--main-bg);
    background-color: var(--eligible-drop);
}

.watermark {
    max-height: 100%;
    width: 100%;
    opacity: 0.4;
    text-align: center;
    font-size: 2.3rem;
    font-weight: bold;
    margin: 0;
}

.team-wrapper {
    display: flex;
    flex-direction: column;
    padding: 10px 5px;
    background-color: var(--alt-bg);
    border-radius: 1px;
    text-align: left;
    align-items: center;
    color: var(--alt-text);
    margin: 0 0 15px;
}

.logo-wrapper {
    display: flex;
    align-items: center;
}

.seed {
    border: 1px solid white;
    width: 18px;
    height: 18px;
    text-align: center;
    border-radius: 50px;
    color: black;
    background-color: white;
    margin-right: 3px;
}

.team-logo {
    max-height: 40px;
    margin: auto;
}

.App {
  text-align: center;
  font-family: 'Karla', sans-serif;
  /* font-family: 'Rubik', sans-serif; */
}

:root {
  --main-bg: #ffffff;
  --alt-bg: #637282;
  --main-text:  #1b2733;
  --alt-text:  #ffffff;
  --button: #ab7124;
  --eligible-drop: #00ff99;
}

/* ðŸŽ¨ colors: #ffffff | #1b2733 | #637282 | #AB24A1 */

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.logo {
  height: 80px;
  pointer-events: none;
  position: absolute;
  left: 15px;
  top: 10px;
}

@media (prefers-reduced-motion: no-preference) {
  .Nhl-logo {
    -webkit-animation: nhl-logo-spin infinite 60s linear;
            animation: nhl-logo-spin infinite 60s linear;
  }
}

.App-header {
  background-color: #637282;
  background-color: var(--alt-bg);
  position: absolute;
  height: 100px;
  width: 100px;
  min-height: 100vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes nhl-logo-spin {
  0% {
    transform: rotate(0deg);
  }
  45% {
    transform: rotate(5deg);
  }
  46% {
    transform: rotate(175deg);
  }
  54% {
    transform: rotate(185deg);
  }
  55% {
    transform: rotate(355deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes nhl-logo-spin {
  0% {
    transform: rotate(0deg);
  }
  45% {
    transform: rotate(5deg);
  }
  46% {
    transform: rotate(175deg);
  }
  54% {
    transform: rotate(185deg);
  }
  55% {
    transform: rotate(355deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

